/* technical-support-ui/src/pages/TicketListPage.css */
.ticket-list-page h2 {
  margin-top: 0;
  margin-bottom: 24px;
}

.page-actions {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  flex-wrap: wrap;
  gap: 16px;
}

.create-ticket-btn {
  background: #2980ef;
  color: #fff;
  border: none;
  border-radius: 6px;
  padding: 10px 20px;
  font-weight: 600;
  font-size: 16px;
  cursor: pointer;
  text-decoration: none;
  /* For Link component */
  display: inline-block;
  transition: background-color 0.2s;
}

.create-ticket-btn:hover {
  background: #1f6ac5;
}

/* Placeholder for filters */
.filters-panel {
  background: #fff;
  padding: 16px;
  border-radius: 8px;
  border: 1px solid #e5e5e5;
  margin-bottom: 24px;
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  align-items: center;
}

.filters-panel input,
.filters-panel select {
  padding: 8px 12px;
  border-radius: 6px;
  border: 1px solid #ccc;
  min-width: 180px;
}

.filters-panel button {
  padding: 8px 16px;
  border-radius: 6px;
  cursor: pointer;
  border: 1px solid transparent;
}

.apply-btn {
  background: #2ecc71;
  color: #fff;
  border-color: #2ecc71;
}

.reset-btn {
  background: #f0f0f0;
  color: #333;
  border-color: #ccc;
}


.loading-container,
.no-tickets-container {
  text-align: center;
  margin-top: 80px;
  color: #666;
  padding: 40px;
  background: #fff;
  border: 1px solid #e5e5e5;
  border-radius: 8px;
}

.spinner {
  width: 48px;
  height: 48px;
  border: 6px solid #eee;
  border-top: 6px solid #2980ef;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin: 0 auto 16px;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.tickets-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(340px, 1fr));
  gap: 24px;
}

.ticket-card {
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  border: 1px solid #e5e5e5;
  padding: 24px;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  transition: box-shadow 0.2s, transform 0.2s;
  border-left: 6px solid #bbb;
}

.ticket-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.08);
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 8px;
  gap: 8px;
}

.ticket-title {
  font-weight: 700;
  font-size: 18px;
  color: #333;
  margin: 0;
  word-break: break-word;
}

.card-tags {
  display: flex;
  gap: 8px;
  flex-shrink: 0;
}

.tag {
  color: #fff;
  border-radius: 8px;
  padding: 2px 10px;
  font-size: 13px;
  font-weight: 600;
  white-space: nowrap;
}

.ticket-meta {
  color: #888;
  font-size: 14px;
  margin-bottom: 12px;
}

.ticket-description {
  color: #444;
  font-size: 15px;
  flex-grow: 1;
  line-height: 1.5;
  margin: 0;
}

.card-footer {
  margin-top: 16px;
  padding-top: 16px;
  border-top: 1px solid #f0f0f0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 14px;
  color: #666;
}

.assignee-info {
  font-weight: 500;
}