/* technical-support-ui/src/pages/forms.css */
.form-container {
  max-width: 420px;
  margin: 60px auto;
  background: #fff;
  padding: 32px;
  border-radius: 12px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.07);
}

.form-container h2 {
  text-align: center;
  margin-top: 0;
  margin-bottom: 24px;
  color: #333;
}

.form-group {
  margin-bottom: 18px;
}

.form-group label {
  display: block;
  margin-bottom: 6px;
  font-weight: 600;
  color: #555;
}

.form-group .required-mark {
  color: #e74c3c;
}

.form-input,
.form-textarea,
.form-select {
  width: 100%;
  padding: 10px 12px;
  border-radius: 8px;
  border: 1px solid #ccc;
  font-size: 16px;
  box-sizing: border-box;
  transition: border-color 0.2s, box-shadow 0.2s;
}

.form-input:focus,
.form-textarea:focus,
.form-select:focus {
  outline: none;
  border-color: #2980ef;
  box-shadow: 0 0 0 3px rgba(41, 128, 239, 0.2);
}

.form-textarea {
  min-height: 100px;
  resize: vertical;
}

.form-button {
  width: 100%;
  padding: 12px;
  border-radius: 8px;
  background: #2980ef;
  color: #fff;
  border: none;
  font-weight: 700;
  font-size: 17px;
  cursor: pointer;
  transition: background-color 0.2s;
}

.form-button:disabled {
  background: #bbb;
  cursor: not-allowed;
}

.form-button:not(:disabled):hover {
  background: #1f6ac5;
}

.form-button-secondary {
  margin-top: 12px;
  background: #fff;
  color: #2980ef;
  border: 1px solid #2980ef;
}

.form-button-secondary:hover {
  background: #f0f6ff;
}

.form-error {
  color: #e74c3c;
  margin-bottom: 12px;
  text-align: center;
}

.form-success {
  color: #2ecc71;
  margin-bottom: 12px;
  text-align: center;
}

.form-footer-link {
  text-align: center;
  margin-top: 20px;
  font-size: 15px;
}

.form-footer-link a {
  color: #2980ef;
  font-weight: 600;
  text-decoration: none;
}

.form-footer-link a:hover {
  text-decoration: underline;
} 