/* technical-support-ui/src/components/Sidebar.css */
.sidebar {
  width: 240px;
  background: #23282d;
  color: #eee;
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
}

.sidebar-header {
  padding: 18px 20px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.logo {
  font-size: 20px;
  font-weight: 700;
  color: #fff;
  text-decoration: none;
}

.sidebar-nav {
  flex-grow: 1;
  overflow-y: auto;
}

.sidebar-nav ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.menu-item>a,
.menu-item>span {
  display: flex;
  align-items: center;
  padding: 12px 20px;
  color: #eee;
  text-decoration: none;
  font-weight: 600;
  transition: background-color 0.2s;
  cursor: pointer;
  border-left: 4px solid transparent;
}

.menu-item>a:hover,
.menu-item>span:hover {
  background-color: #333940;
}

.ticket-count {
  background-color: #0073aa;
  color: #fff;
  border-radius: 10px;
  padding: 2px 8px;
  font-size: 12px;
  font-weight: 700;
  margin-left: auto;
}

.submenu {
  list-style: none;
  padding-left: 0;
  background-color: #333940;
}

.submenu li a {
  display: block;
  padding: 10px 20px 10px 44px;
  color: #ccc;
  text-decoration: none;
  font-size: 14px;
  transition: color 0.2s, padding-left 0.2s;
}

.submenu li a:hover {
  color: #00b9eb;
  padding-left: 48px;
}

.submenu li a.active {
  color: #fff;
  font-weight: 600;
  background-color: #2a2f34;
}

.pro-features {
  margin-top: auto;
  padding: 16px 20px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.pro-features a {
  display: block;
  text-align: center;
  padding: 10px;
  background-color: #ffba00;
  color: #333;
  border-radius: 6px;
  text-decoration: none;
  font-weight: 700;
  transition: background-color 0.2s;
}

.pro-features a:hover {
  background-color: #ffc933;
}