/* technical-support-ui/src/pages/TicketDetailPage.css */
.ticket-detail-layout {
  display: flex;
  gap: 24px;
}

.discussion-column {
  flex: 3;
  min-width: 0;
  /* Important for flexbox to shrink */
}

.info-column {
  flex: 1;
  min-width: 280px;
  /* Ensure info column doesn't get too small */
}

.ticket-panel {
  background: #fff;
  border: 1px solid #e5e5e5;
  border-radius: 8px;
  margin-bottom: 24px;
}

.panel-header {
  padding: 12px 20px;
  border-bottom: 1px solid #e5e5e5;
  font-size: 16px;
  font-weight: 600;
  color: #333;
}

.panel-body {
  padding: 20px;
}

/* Reply Form */
.reply-form .reply-textarea {
  width: 100%;
  min-height: 120px;
  border-radius: 6px;
  border: 1px solid #ccc;
  padding: 10px;
  font-size: 15px;
  resize: vertical;
  margin-bottom: 12px;
}

.reply-actions {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 12px;
}

.action-buttons button {
  background: #f0f0f0;
  border: 1px solid #ccc;
  border-radius: 6px;
  padding: 6px 12px;
  cursor: not-allowed;
  opacity: 0.7;
}

.submit-button {
  background: #2980ef;
  color: #fff;
  border: none;
  border-radius: 6px;
  padding: 10px 22px;
  font-weight: 600;
  font-size: 15px;
  cursor: pointer;
  transition: background-color 0.2s;
}

.submit-button:hover {
  background: #1f6ac5;
}

/* Conversation Thread */
.conversation-thread {
  display: flex;
  flex-direction: column;
  gap: 16px;
  max-height: 500px;
  overflow-y: auto;
  padding: 10px 10px 10px 0;
}

.comment-wrapper {
  display: flex;
  align-items: flex-start;
  gap: 12px;
}

.comment-wrapper.is-user {
  flex-direction: row-reverse;
}

.comment-avatar {
  width: 38px;
  height: 38px;
  background: #2980ef;
  color: #fff;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 17px;
  flex-shrink: 0;
  text-transform: uppercase;
}

.comment-box {
  background: #f1f3f5;
  border-radius: 12px;
  padding: 10px 18px;
  max-width: 80%;
  border: 1px solid #e5e5e5;
}

.comment-wrapper.is-user .comment-box {
  background: #eaf3ff;
  border-color: #c9e0ff;
}

.comment-header {
  font-weight: 700;
  color: #333;
  margin-bottom: 4px;
}

.comment-content {
  font-weight: 500;
  color: #222;
  white-space: pre-wrap;
  word-break: break-word;
}

.comment-meta {
  color: #888;
  font-size: 13px;
  margin-top: 6px;
  text-align: right;
}

/* Info Column */
.info-group {
  margin-bottom: 18px;
}

.info-group label {
  display: block;
  font-weight: 600;
  color: #555;
  margin-bottom: 6px;
}

.info-group select,
.info-group p {
  width: 100%;
  padding: 8px;
  border-radius: 6px;
  border: 1px solid #ccc;
  font-size: 15px;
  margin: 0;
}

.info-group p {
  background-color: #f9f9f9;
}