/* technical-support-ui/src/components/Layout.css */
/* This file is repurposed for the new main layout */

.app-layout {
  display: flex;
  min-height: 100vh;
  background-color: #f0f2f5;
  font-family: 'Segoe UI', 'Roboto', Arial, sans-serif;
}

.main-wrapper {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
  /* Prevent horizontal scroll from content */
}

.content-area {
  flex: 1;
  padding: 24px;
}

/* --- Header --- */
.app-header {
  background: #fff;
  color: #333;
  height: 64px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 24px;
  border-bottom: 1px solid #ddd;
  flex-shrink: 0;
}

.user-info {
  display: flex;
  align-items: center;
  gap: 16px;
}

.user-info span {
  font-weight: 500;
}

.logout-button {
  background: #f0f0f0;
  border: 1px solid #ccc;
  color: #333;
  padding: 6px 14px;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 600;
  transition: background-color 0.2s, border-color 0.2s;
}

.logout-button:hover {
  background: #e0e0e0;
  border-color: #bbb;
}

/* --- Footer --- */
.app-footer {
  padding: 12px 24px;
  background: #fff;
  border-top: 1px solid #ddd;
  color: #666;
  font-size: 14px;
  display: flex;
  justify-content: space-between;
  flex-shrink: 0;
}

.app-footer p {
  margin: 0;
}